body{
	width: 60%;
	margin: 0 auto;
	background-color: #454E57;
	margin-top: 15px;
	font-family: Open Sans;
  color: white;
	margin-bottom: 30px;
}

.App-header{
  background: #454E57;
  list-style-type: none;
  margin-bottom: 30px;
}

nav{
  margin-left: 20%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}


.header{
  text-align: center;
  color: white;
  padding: 15px;
}

.header p{
  margin-top: -10px;
}

.section-name{
  font-size: 15px;
  color: white;
  border-bottom: 1px ridge #80b3ff;
  padding-top: 30px;
  margin-bottom: 50px;
}

.valid{
  background-color: #454E57;
  border-bottom: 1px gray;
  border-style: hidden hidden ridge hidden;
  padding-top: 27px;
	color: white;
}

.invalid{
  background-color: #454E57;
  border: solid 1px lightcoral;
  padding-top: 27px;
	color: white;
}

.top{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-bottom: 18px;
}

.top-left{
  width: 75%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



#address{
	border: 1px ridge gray;
  margin-top: 25px;
  margin-bottom: 35px;
	line-height: 41px;
	background-image: -webkit-linear-gradient(#454E57 35px, #ccc 36px, #454E57 35px);
	background-image: -o-linear-gradient(#454E57 35px, #ccc 36px, #454E57 35px);
	background-image: linear-gradient(#454E57 35px, #ccc 36px, #454E57 35px);
	color: white;
}

.low-middle-top{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.middle-top > input[type="text"]{
  width: 100%;
}

.column-selection{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.row-selection{
	list-style-type: none;
	display: -ms-flexbox;
	display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
	padding: 0;
}

.row-selection li {
    width: 180px;
    height: 35px;
}

.row-selection label, .row-selection input {
		display: block;
		text-align: center;
}

.row-selection input[type="radio"] {
    display: none;
}

.row-selection input[type="radio"]:checked + label {
    background: #00bfff;
}

.row-selection label {
     padding:5px;
     border:1px solid #CCC;
     cursor:pointer;
}

.row-selection label:hover {
     background:#DDD;
}


.col-down{
		list-style-type: none;
		margin-left: -40px;
}

.col-down input[type="checkbox"] {
		visibility: hidden;
		width: 0;
		height: 0;
		margin: 0;
}

.col-down input[type="checkbox"] + label:before {
		content: '';
		background: #454E57;
		border: 1px solid white;
		display: inline-block;
		width: 10px;
		height: 10px;
		margin-right: 10px;
}

.col-down input[type="checkbox"]:checked + label:before{
    background: #00bfff;
}


.col{
  width: 45%;
}



#portfolio{
  width: 100%;
}

#else{
  border: 1px ridge gray;
  margin-top: 25px;
  margin-bottom: 35px;
  background-color: #454E57;
	color: white;
}

#submit{
  padding: 0 65px;
  display: block;
  margin: 0 auto;
  background-color: #00bfff;
  border: none;
	height: 40px;
}



footer{
  text-align: center;
  margin-top: 50px;
}

button{
  border-radius: 5px;
  border-style: hidden;
}

button:hover{
  background: black;
  color: white;
}

.footer-btn{
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

@media only screen and (max-width: 600px){
	.top,
	.column-selection{
	  display: -ms-flexbox;
	  display: flex;
	  -ms-flex-direction: column;
	      flex-direction: column;
	}

	.top,
	.top-left, /*normal: top-left width 75%*/
	.top-right > input,
	.low-middle-top > input,
	.col{
		width: 100%;
	}

	.row-selection{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}
}

@media screen and (max-width: 1100px) {
	.low-middle-top{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}
}



